import { Component, Injector, OnInit } from '@angular/core';
import { FormArray, FormControl } from '@angular/forms';
import { BaseForm } from 'src/app/class/base.form';
import { URLz } from 'src/app/enums/url.enum';
import { Organaization } from 'src/app/interface/org';
@Component({
  selector: 'app-org-add',
  templateUrl: './org-add.component.html',
  styleUrls: ['./org-add.component.css'],
})
export class OrgAddComponent extends BaseForm implements OnInit {
  constructor(injector: Injector,
    ) {
    super(injector);
    this.param.endpoint = URLz.ORG
  }
  ngOnInit(): void {
    this._activeId = this._fhs._getURLParam('id')
    this._fhs._pathLocation = '/organaization/org';
    this.initform();
    if (this.emptyCheck(this._activeId)) {
      this.patchData();
    }
  }
  initform() {
    this._fs._form = this._fb.group({
      title: ['',this._vs._val('Donation Org', {minChar: 3, maxChar: 100})],
      system_id:  this._fb.array([], this._vs._vals('System')),
    });
  }

  patchData() {
    this._http.get( {...this.param, resource: this._activeId})
    .subscribe(res => {
      let data  = res.data.row as Organaization
      this._fs._form.patchValue({
        title: data.title,
        system_id: data.system_id
      });
      data.organisation_system.forEach(org => {
        (<FormArray>this._fs._form.get('system_id'))
        .push(new FormControl(org.id))
      })
    });
  }
}
