import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { BaseListClass } from 'src/app/class/base-list-class';
import { ACTION } from 'src/app/enums/action.enum';

@Component({
  selector: 'app-org-list',
  templateUrl: './org-list.component.html',
  styleUrls: ['./org-list.component.css']
})
export class OrgListComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this._pathLocation = "/organaization/org_add";
    this._component = "Donation Org"
    this.initTables();
  }

  initTables(){
    this._reset(URLz.ORG)
    this._tbls[URLz.ORG].columns = ['id', 'actions', 'title', 'system_id'];
    if(this._ss.checkPermission(ACTION.STATUS)){
      this._tbls[URLz.$].columns.splice(2, 0, 'activate')
    }
    this._tbls[URLz.ORG].endpoint = URLz.ORG;
    this._formCreator(this._tbls[URLz.ORG]);
    this._refresh(URLz.ORG)
  }
}
